@charset "UTF-8";

@font-face {
  font-family: "PlayfairDisplay";
  src: url("../fonts/PlayfairDisplay-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "PTSerif";
  src: url("../fonts/PTSerif-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

:root {
  --clr-light: #fff;
  --clr-main: #000;
  --clr-border: #c2b7b0;
  --clr-bg-main: #fffefd;
  --clr-bg-secondary: #f8f8f8;
  --clr-breadcrumb: #6a6a6a;
  --clr-footer: #d1d1d1;
  --radius: 4px;
}

html {
  scroll-behavior: smooth;
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

html,
body {
  height: 100%;
}

body {
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}

aside,
nav,
footer,
header,
section,
main {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  vertical-align: top;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  /* 2 */
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
}

button,
input {
  /* 1 */
  overflow: visible;
}

button,
select {
  /* 1 */
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
  background-color: transparent;
  cursor: pointer;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

input::-ms-clear {
  display: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  padding: 0;
  /* 3 */
  white-space: normal;
  /* 1 */
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

ul li {
  list-style: none;
}

body {
  font-family: "PTSerif", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: var(--clr-main);
  background-color: var(--clr-bg-main);
}

body.lock {
  overflow: hidden;
}

body.lock-sidebar .wrapper::before {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s, visibility 0.3s;
}

.wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
}

.wrapper::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
  max-width: 1750px;
}

@keyframes arrow-bounce {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-30%);
  }

  100% {
    transform: translateY(0);
  }
}

.hide {
  display: none;
}

.btn-primary {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;
  border-radius: var(--radius);
  width: 100%;
  height: 60px;
  max-width: 300px;
  font-size: 18px;
  color: var(--clr-light);
  background-color: var(--clr-main);
}

.section__title {
  font-size: 42px;
  position: relative;
  padding-right: 15px;
  padding-left: 15px;
  font-family: "PlayfairDisplay", "PTSerif", sans-serif;
  text-align: center;
}

.section__title svg {
  width: 22px;
  height: 22px;
}

.section__subtitle {
  font-size: 16px;
  margin-top: 25px;
  padding-right: 15px;
  padding-left: 15px;
  line-height: 140%;
  text-align: center;
}

.pd__105t {
  padding-top: 105px;
}

.pd__45t {
  padding-top: 45px;
}

.pd__header {
  padding-top: 80px;
}

.answer {
  margin-top: 15px;
  font-size: 18px;
  text-align: center;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

button[type="submit"][disabled] {
  opacity: 0.7;
  cursor: default;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  width: 100%;
  height: 80px;
}

.header.header-main .header__nav-link::after {
  background-color: var(--clr-light);
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  width: 100%;
  max-width: 97px;
  max-height: 65px;
}

.header__logo img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.header__nav-list {
  display: flex;
  align-items: center;
}

.header__nav-item:not(:last-child) {
  margin-right: 30px;
}

.header__nav-link {
  position: relative;
  padding-top: 29px;
  padding-bottom: 29px;
}

.header__contact {
  display: none;
}

.header__contact-link {
  display: inline-block;
  font-size: 18px;
  opacity: 0;
  transform: translateY(100%);
  transition: opacity 0.6s, transform 0.6s;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
}

.header__cart svg {
  width: 44px;
  height: 44px;
}

.header__burger {
  display: none;
}

.breadcrumbs {
  padding-top: 25px;
  padding-bottom: 25px;
}

.breadcrumbs__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-size: 16px;
  color: var(--clr-breadcrumb);
}

.breadcrumbs__item {
  position: relative;
  white-space: nowrap;
}

.breadcrumbs__item:not(:last-child)::after {
  content: "/";
  margin-right: 6px;
  margin-left: 2px;
}

.breadcrumbs__item--active {
  color: var(--clr-main);
}

.main {
  position: relative;
  z-index: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  min-height: 500px;
}

.main__background {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.main__background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main__background::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}

.main__wrapper {
  margin-right: auto;
  margin-left: auto;
  max-width: 645px;
  text-align: center;
  color: var(--clr-light);
}

.main__title {
  font-size: 56px;
  margin-bottom: 25px;
  font-family: "PlayfairDisplay", "PTSerif", sans-serif;
}

.main__subtitle {
  margin-right: auto;
  margin-bottom: 25px;
  margin-left: auto;
  max-width: 520px;
  line-height: 130%;
}

.main__arrow {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  animation-name: arrow-bounce;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

.main__arrow svg {
  width: 10px;
  height: 20px;
  fill: var(--clr-light);
  opacity: 0.7;
  transition: opacity 0.3s;
}

.about-main .container {
  max-width: 1600px;
}

.about-main__wrapper {
  position: relative;
}

.about-main__subtitle {
  margin-right: auto;
  margin-left: auto;
  max-width: 485px;
}

.about-main__tabs-head {
  margin-bottom: 25px;
  font-size: 20px;
}

.about-main__tabs-head-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid var(--clr-border);
  padding-right: 15px;
  padding-bottom: 5px;
  padding-left: 15px;
  white-space: nowrap;
  text-align: center;
  transition: border-bottom 0.3s;
  cursor: pointer;
}

.about-main__tabs-head-item svg {
  margin-bottom: 5px;
  width: 35px;
  height: 35px;
  fill: var(--clr-main);
  pointer-events: none;
}

.about-main__tabs-content-text {
  margin-right: 35px;
  margin-bottom: 25px;
  margin-left: 35px;
  line-height: 130%;
}

.about-main__tabs-content-link {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
}

.about-main__tabs-content-link svg {
  margin-left: 10px;
  width: 10px;
  height: 20px;
  opacity: 0.7;
  transform: rotate(-90deg);
}

.bestsellers__wrapper {
  display: flex;
  flex-wrap: wrap;
}

.bestsellers__item {
  margin-right: 1.7%;
  margin-bottom: 40px;
  width: calc((100% / 3) - (3.4% / 3));
  text-align: center;
}

.bestsellers__item-image {
  position: relative;
  margin-bottom: 15px;
  border-radius: var(--radius);
  padding-top: 93.25%;
}

.bestsellers__item-image img {
  position: absolute;
  left: 0;
  top: 0;
  border-radius: var(--radius);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.bestsellers__item-title {
  font-size: 24px;
}

.philosophy {
  margin-top: 105px;
  padding-top: 195px;
  padding-bottom: 50px;
  background-image: url("");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #ccc;
}

.philosophy__wrapper {
  margin-right: auto;
  margin-left: auto;
  max-width: 970px;
  text-align: center;
}

.philosophy__quote {
  font-size: 24px;
  margin-top: 25px;
  margin-bottom: 125px;
  line-height: 105%;
}

.own-desing__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.own-desing__info {
  flex: 0 1 45%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  max-width: 605px;
  text-align: center;
}

.own-desing__info p {
  margin-bottom: 25px;
  line-height: 130%;
}

.own-desing__image {
  position: relative;
  flex: 0 1 55%;
  border-radius: var(--radius);
  padding-top: 35%;
}

.own-desing__image img {
  position: absolute;
  left: 0;
  top: 0;
  border-radius: var(--radius);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.advantages__subtitle {
  margin-right: auto;
  margin-left: auto;
  max-width: 710px;
}

.advantages__wrapper {
  display: flex;
  flex-wrap: wrap;
}

.advantages__item {
  position: relative;
  margin-right: 1%;
  margin-bottom: 1%;
  width: calc((100% / 2) - (1% / 2));
  text-align: center;
}

.advantages__image {
  position: relative;
  z-index: 0;
  border-radius: var(--radius);
  padding-top: 58.42%;
}

.advantages__image img {
  position: absolute;
  left: 0;
  top: 0;
  border-radius: var(--radius);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.advantages__image::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.65);
}

.advantages__item-info {
  position: absolute;
  left: 50%;
  top: 50%;
  padding-right: 25px;
  padding-left: 25px;
  width: 100%;
  transform: translate(-50%, -50%);
}

.advantages__title {
  font-size: 1.5vw;
  text-align: center;
  color: var(--clr-light);
  transform: translateY(100%);
  transition: transform 0.3s;
}

.advantages__info {
  font-size: 1.2vw;
  color: var(--clr-light);
  opacity: 0;
  visibility: hidden;
  transform: translateY(100%);
  transition: transform 0.3s, opacity 0.3s, visibility 0.3s;
}

.insta__main-link {
  display: block;
  margin: 25px auto;
  text-align: center;
}

.insta__subtitle {
  margin-right: auto;
  margin-left: auto;
  max-width: 960px;
}

.insta__wrapper .instagram_gallery {
  display: flex;
  flex-wrap: wrap;
}

.insta__wrapper a {
  display: block;
  margin-right: 1.7%;
  margin-bottom: 1.7%;
  width: calc((100% / 4) - (5.1% / 4));
}

.insta__wrapper a img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.contacts__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.contacts__socials {
  flex-shrink: 0;
  margin-right: 15px;
  width: 100%;
  max-width: 415px;
}

.contacts__social {
  padding: 15px 30px;
  background-color: var(--clr-bg-secondary);
}

.contacts__social:not(:last-child) {
  margin-bottom: 15px;
}

.contacts__social-title {
  margin-bottom: 5px;
  color: var(--clr-breadcrumb);
}

.contacts__image {
  width: 100%;
  max-width: 1225px;
  max-height: 562px;
}

.contacts__image img {
  width: 100%;
  height: 100%;
  max-height: 562px;
  object-fit: cover;
}

.contacts__social-link {
  font-size: 18px;
}

.contacts__bottom {
  margin-top: 105px;
  padding-top: 105px;
  padding-bottom: 105px;
  background-color: var(--clr-border);
}

.contacts__bottom-info {
  font-size: 24px;
  margin-bottom: 25px;
  max-width: 580px;
  font-family: "PlayfairDisplay", "PTSerif", sans-serif;
}

.contacts__form-wrapper {
  display: flex;
  margin-bottom: 35px;
}

.contacts__form-wrapper .btn-primary {
  margin-left: 0;
}

.contacts__form-wrapper + .contacts__input-wrapper {
  max-width: 400px;
}

.contacts__input-wrapper {
  position: relative;
  margin-right: 15px;
  width: 100%;
  max-width: 280px;
}

.contacts__input {
  border: 1px solid var(--clr-main);
  padding: 6px 20px;
  width: 100%;
  height: 60px;
  background-color: transparent;
  transition: border-bottom 0.3s;
}

.contacts__input:hover,
.contacts__input:focus {
  outline: none;
  border: 1px solid var(--clr-light);
  transition: border-bottom 0.3s;
}

.contacts__label {
  position: absolute;
  left: 20px;
  top: 20px;
  padding-right: 5px;
  padding-left: 5px;
  color: var(--clr-main);
  background-color: var(--clr-border);
  transition: transform 0.3s;
  pointer-events: none;
}

.contacts__label--focus {
  transform: translate(-10px, -30px);
  transition: transform 0.3s;
}

.contacts__label--checkbox {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 30px;
  cursor: pointer;
}

.contacts__label--checkbox input {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.contacts__label--checkbox input:checked + span::before {
  opacity: 1;
  visibility: visible;
}

.contacts__label--checkbox input:focus + span {
  border: 1px solid var(--clr-light);
}

.contacts__label--checkbox span {
  position: absolute;
  left: 0;
  top: 50%;
  border-radius: var(--radius);
  width: 20px;
  height: 20px;
  background-color: var(--clr-main);
  transform: translateY(-50%);
}

.contacts__label--checkbox span::before {
  content: "âœ“";
  position: absolute;
  left: 50%;
  top: 50%;
  color: var(--clr-light);
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, -50%);
}

.contacts .contacts__label--checkbox + .js-validate-error-label {
  top: 23px;
}

.contacts .js-validate-error-label {
  position: absolute;
  left: 6px;
  top: 63px;
}

.catalog__wrapper {
  display: flex;
  flex-wrap: wrap;
}

.catalog__item {
  position: relative;
  padding-top: 43.71%;
  width: 33.333%;
}

.catalog__item img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.catalog__item a::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.45s, visibility 0.45s;
}

.catalog__item-title {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1;
  margin-right: 15px;
  margin-left: 15px;
  font-family: "PlayfairDisplay", "PTSerif", sans-serif;
  font-size: 2.95vw;
  text-align: center;
  color: var(--clr-light);
  transform: translate(-50%, -50%);
}

.catalog__item-title::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -5px;
  width: 0;
  height: 2px;
  background-color: var(--clr-light);
  transform: translateX(-50%);
  transition: width 0.45s;
}

.category__wrapper {
  display: flex;
  align-items: flex-start;
}

.category__sidebar {
  width: 100%;
  max-width: 290px;
}

.category__sidebar-item:not(:last-child) {
  margin-bottom: 35px;
}

.category__sidebar-item--active .category__sidebar-arrow svg {
  transform: rotate(0);
  transition: transform 0.3s;
}

.category__sidebar-item--active .category__sidebar-title::after {
  width: 50px;
  transition: width 0.45s;
}

.category__sidebar-btn {
  position: fixed;
  left: -50px;
  top: 15px;
  z-index: 10;
  border-radius: 0 var(--radius) var(--radius) 0;
  width: 40px;
  height: 40px;
  background-color: var(--clr-main);
  transition: left 0.3s;
}

.category__sidebar-btn::after,
.category__sidebar-btn::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 80%;
  height: 2px;
  background-color: var(--clr-light);
}

.category__sidebar-btn::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.category__sidebar-btn::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.category__sidebar-btn:focus {
  outline: 0;
}

.category__sidebar-title {
  font-size: 24px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "PlayfairDisplay", "PTSerif", sans-serif;
}

.category__sidebar-title::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 0;
  height: 2px;
