.swiper-button-prev,
.swiper-button-next {
    position: absolute;
    top: 55%;
    z-index: 10;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

    &.swiper-button-disabled {
        opacity: 0.35;
        cursor: auto;
        pointer-events: none;
    }
    &:after {
        width: 25px;
        height: 40px;
        background-image: url("../img/ui/slider-arrow.svg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
}
.swiper-button-prev {
    &:after {
        content: "";
    }
    left: 0;
    right: auto;
}
.swiper-button-next {
    &:after {
        content: "";
    }
    right: 0;
    left: auto;
    transform: rotate(180deg);
}