//<ÐžÐ‘ÐÐ£Ð›Ð•ÐÐ˜Ð•, ÐžÐ‘Ð©Ð˜Ð• ÐŸÐÐ ÐÐœÐ•Ð¢Ð Ð«>===============================================================================================
:root {
    --clr-light: #fff;
    --clr-main: #000;
    --clr-border: #c2b7b0;
    --clr-bg-main: #fffefd;
    --clr-bg-secondary: #f8f8f8;
    --clr-breadcrumb: #6a6a6a;
    --clr-footer: #d1d1d1;
    --radius: 4px;
}

@import "normalize";

body {
    font-family: $font-family;
    font-weight: 400;
    font-size: 16px;
    color: var(--clr-main);
    background-color: var(--clr-bg-main);

    &.lock {
        overflow: hidden;
    }

    &.lock-sidebar {
        .wrapper::before {
            opacity: 1;
            visibility: visible;
            transition: opacity 0.3s, visibility 0.3s;
        }
    }
}

//</ÐžÐ‘ÐÐ£Ð›Ð•ÐÐ˜Ð•, ÐžÐ‘Ð©Ð˜Ð• ÐŸÐÐ ÐÐœÐ•Ð¢Ð Ð«>===============================================================================================

//<ÐžÐ‘ÐžÐ›ÐžÐ§ÐšÐ>===========================================================================================================
.wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
    min-width: 320px;
    min-height: 100%;

    &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 5;
        background-color: rgba(0, 0, 0, 0.6);
        opacity: 0;
        visibility: hidden;
        transition: opacity $ms, visibility $ms;
    }
}

//</ÐžÐ‘ÐžÐ›ÐžÐ§ÐšÐ>===========================================================================================================

//<ÐžÐ¡ÐÐžÐ’ÐÐÐ¯ Ð¡Ð•Ð¢ÐšÐ>===========================================================================================================
.container {
    margin: 0 auto;
    padding: 0 15px;
    width: 100%;
    max-width: $mw + px;
}

